body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #cfd2b8;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.container {
  grid-template-columns: repeat(auto-fit, 250px);
  gap: 10px 20px;
  display: grid;
}

.boxCard {
  width: 200px;
  height: 350px;
  background-color: #b0d5e1;
  outline: 1px solid #a59898;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  padding: 4px;
  display: flex;
  box-shadow: 1px -1px 9px rgba(87, 80, 78, .59);
}

.cardText {
  font-size: 14px;
}

.dotsCard {
  color: red;
}

.show-more .dotsCard {
  display: none;
}

.show-more .additionalText {
  display: inline;
}

.additionalText {
  display: none;
}

.btnReadMore {
  width: 150px;
  height: 20px;
  color: red;
  padding: 4px;
}

.imgCard {
  width: 100%;
  height: 50%;
  object-fit: cover;
  display: block;
}

